@import url(https://fonts.googleapis.com/css2?family=Brygada+1918&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

    .navbar-toggler-icon::before {
        -webkit-transform: translateY(-10px);
                transform: translateY(-10px);
      }
      .navbar-toggler-icon ::after {
        -webkit-transform: translateY(10px);
                transform: translateY(10px);
      }
      
      /* Animation */
      .navbar-toggler-icon .open .navbar-toggler-icon  {
        -webkit-transform: translateX(-50px);
                transform: translateX(-50px);
        background: transparent;
        box-shadow: none;
      }
      
      .navbar-toggler-icon .open .navbar-toggler-icon ::before {
        -webkit-transform: rotate(45deg) translate(35px, -35px);
                transform: rotate(45deg) translate(35px, -35px);
      }
      
      .navbar-toggler-icon  .navbar-toggler-icon ::after {
        -webkit-transform: rotate(-45deg) translate(35px, 35px);
                transform: rotate(-45deg) translate(35px, 35px);
      }

      .navbar-background {
          background-color: green;
      }

      .logo-Margin {
         margin-right: 10px;
      }

      .navbar-margin {
          margin-left: 10px;
      }

